body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.boddy{
  background: #000000;
}
.navbar{
  display: flex;
  flex-direction: column;
  background: #191919;
  font-family: Inter;
  color: #ebebeb;
}
.navbar-top{
  display: flex;
  align-items: center;
  align-content: center;
}
.search-option::-webkit-input-placeholder{
  font-family: 'Work';
  font-size: 16px;
  color: #8C8CA1;
  line-height: 140%;
}
.search-option:-ms-input-placeholder{
  font-family: 'Work';
  font-size: 16px;
  color: #8C8CA1;
  line-height: 140%;
}
.search-option::-ms-input-placeholder{
  font-family: 'Work';
  font-size: 16px;
  color: #8C8CA1;
  line-height: 140%;
}
.search-option::placeholder{
  font-family: 'Work';
  font-size: 16px;
  color: #8C8CA1;
  line-height: 140%;
}
.search-option{
  margin: 1rem 0;
  border-radius: 10px;
  border-color: white;
  border-width: 0.01rem;
  padding: 0.4rem 0.8rem;
  color: #8c8ca1;
  font-size: 16px;
  background: black;
  font-family: "Work Sans";
}

.title-in{
  /* text-align: center; */
  font-family: "Work Sans";
  display: flex;
  font-size: 18px;
}
.line{
  margin: 1.3rem auto;
  width: 100%;
  border-bottom: 0.1rem solid #575767;
}
li{
  font-size: 14px;
  font-family: "Work Sans";
  list-style: none;
}
.hide{
  display: none;
}
.search-option:focus{
  outline: 0;
  border-color: #0086ff;
  border-width: 0.1rem;
  /* box-shadow: 0 1px 4px rgba(255, 255, 255, 0.8); */
}
.toggled-region{
  transition: height 5s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}
.navbar-image{
  position: absolute;
  left: 20px;
  top: 10px;
  height: 60px;
  width: auto;
  cursor: pointer;
}
.profile-card{
  display: none;
}
.power-attr{
  padding: 1rem;
  margin: 2rem;
  background: #191919;
  border: 2px solid #323232;
  box-sizing: border-box;
  border-radius: 24px;
}
.checkbox-lg .custom-control-label::before{
  content: "";
  background-color: black;
  border: none;
  border-radius: 5px;
  top: 0.05rem;
  left: -1.8rem;
  width: 1.5rem;
  height: 1.5rem;
}
.custom-control-input:focus
{
  border-radius: 5px;
  outline: 0;
}

.custom-checkbox{
  /* padding: 0.1rem; */
  border-radius: 5px;
}
.custom-checkbox
.custom-control-label::before {
  border-radius: 5px;
}
.checkbox-lg .custom-checkbox:checked
.custom-control-label::after{
  border-radius: 5px;
  position: absolute;
  top: 1rem;
  width: 1.4rem;
  height: 1.4rem;
  transform: scale(2);
  background-color: #0086FF !important;
}
.title-out{
  color: #8f8f90;
  font-family: Inter;
  font-size: 20px;
  text-align: center;
  font-weight: bold;
  margin: 1rem 0;
}
.vline{
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  border-bottom: 2px solid #323232;
}
.ccol{
  display: flex;
  margin: 0.3rem 1rem;
}
.custom-control-label{
  color: #CCCDCD;
  padding:0rem 0.5rem;
  font-size: 13px;
  font-weight: 800;
  font-family: Inter;
  font-size: 20px;
  flex: none;
  order: 1;
  align-self: center;
  letter-spacing: 0.04em;
}
@media screen and (min-width:750px){
