    width: 260px;
    border-right: 2px solid #323232;
  }
  .title-in span{
    margin-top: 0px;
  }
  .title-in{
    margin: 0.7rem 1rem;
    line-height: 20px;
  }
  li{
    margin: 0.20rem 0;
  }
  .toggled-region{
    position: absolute;
    left: 30px;
    top: 210px;
  }
  .profile-card{
    display: inline;
    margin-bottom: 1rem;
  }
  .profile-image{
    height: 60px;
    margin: 0 1.2rem 0 1rem;
    border-radius: 35px;
    border: 3px solid #0086ff;
  }
  .remaining{
    position: absolute;
    left: 280px;
    width: 80%;
    top: 20px;
  }
  .power-attr{
    display: flex;
    margin-top: 10px;
    align-content: space-around;
    align-items: center;
  }
  .vline{
