    font-family: Inter;
  }
}
@media screen and (max-width:450px){
  .search-option{
    margin-left: 70px;
  }
  .ccol{
    display: flex;
    margin: 0rem 0.8rem;
    text-align: center;
    align-content: flex-start;
    align-items: flex-start;
