@media only screen and (max-width: 900px) and (min-width: 700px){
  .ccol{
    display: flex;
    margin: 0.5rem 0rem;
    text-align: center;
    align-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
  }
  .navbar{
    width: 255px;
  }
  .remaining{
    left: 260px;
  }
  .title-out{
    margin-left: -0.3rem;
  }
  .each{
    margin-left: 1.4rem;
  }
  .vline{
    margin-left: 1rem;
