@media only screen and (max-width: 1300px) and (min-width: 1200px){
  .navbar{
    width: 250px;
  }
  .remaining{
    left: 250px;
  }
  .each{
    margin-left: -1rem;
  }
  .custom-control-label{
    font-size: 12px;
  }
